<script setup>
import { ref, computed, onMounted, onUnmounted } from 'vue';

// Êó•ÂéÜÊï∞ÊçÆ
const currentDate = ref(new Date());
const selectedDate = ref(null);
const eventList = ref([]);

// È¶ñÈ°µÊåâÈíÆËÆæÁΩÆ
const indexSettings = ref({
  showHomeButton: false,
  homeButtonText: 'ËøîÂõûÈ¶ñÈ°µ',
  homeButtonIcon: 'üè†'
});

// ÊòØÂê¶Ê≠£Âú®Âä†ËΩΩËÆæÁΩÆ
const isLoadingSettings = ref(true);

// Ëé∑ÂèñÂΩìÂâçÂπ¥‰ªΩÂíåÊúà‰ªΩ
const currentYear = computed(() => currentDate.value.getFullYear());
const currentMonth = computed(() => currentDate.value.getMonth() + 1);

// Ëé∑ÂèñÂΩìÊúàÂ§©Êï∞
const daysInMonth = computed(() => {
  const year = currentDate.value.getFullYear();
  const month = currentDate.value.getMonth() + 1;
  return new Date(year, month, 0).getDate();
});

// Ëé∑ÂèñÂΩìÊúàÁ¨¨‰∏ÄÂ§©ÊòØÊòüÊúüÂá†
const firstDayOfMonth = computed(() => {
  const year = currentDate.value.getFullYear();
  const month = currentDate.value.getMonth();
  return new Date(year, month, 1).getDay();
});

// ÁîüÊàêÊó•ÂéÜÁΩëÊ†ºÊï∞ÊçÆ
const calendarDays = computed(() => {
  const days = [];
  const totalDays = daysInMonth.value;
  const firstDay = firstDayOfMonth.value;
  
  // Ê∑ªÂä†‰∏ä‰∏™ÊúàÁöÑÊó•ÊúüÂ°´ÂÖÖÁ©∫ÁôΩ
  for (let i = 0; i < firstDay; i++) {
    days.push({
      day: null,
      isCurrentMonth: false,
      hasEvent: false,
      isToday: false
    });
  }
  
  // ÂΩìÂâçÊó•Êúü
  const today = new Date();
  const isCurrentMonthAndYear = today.getFullYear() === currentYear.value && today.getMonth() === currentDate.value.getMonth();
  
  // Ê∑ªÂä†ÂΩìÊúàÁöÑÊó•Êúü
  for (let i = 1; i <= totalDays; i++) {
    const date = new Date(currentYear.value, currentDate.value.getMonth(), i);
    
    // Ê£ÄÊü•ËØ•Êó•ÊúüÊòØÂê¶Êúâ‰∫ã‰ª∂
    const hasEvent = eventList.value.some(event => {
      const eventDate = new Date(event.date);
      return eventDate.getDate() === i && 
             eventDate.getMonth() === date.getMonth() && 
             eventDate.getFullYear() === date.getFullYear();
    });
    
    days.push({
      day: i,
      date: date,
      isCurrentMonth: true,
      hasEvent: hasEvent,
      isToday: isCurrentMonthAndYear && today.getDate() === i,
      isSelected: selectedDate.value && 
                 selectedDate.value.getDate() === i && 
                 selectedDate.value.getMonth() === date.getMonth() && 
                 selectedDate.value.getFullYear() === date.getFullYear()
    });
  }
  
  return days;
});

// Êó•ÂéÜÊéßÂà∂ÊñπÊ≥ï
const goToPreviousMonth = () => {
  const newDate = new Date(currentDate.value);
  newDate.setMonth(newDate.getMonth() - 1);
  currentDate.value = newDate;
};

const goToNextMonth = () => {
  const newDate = new Date(currentDate.value);
  newDate.setMonth(newDate.getMonth() + 1);
  currentDate.value = newDate;
};

const goToToday = () => {
  currentDate.value = new Date();
};

// ÈÄâÊã©Êó•Êúü
const selectDate = (day) => {
  if (day.day === null || !day.isCurrentMonth) return;
  selectedDate.value = day.date;
  
  // Ê†πÊçÆÈÄâ‰∏≠ÁöÑÊó•ÊúüËé∑Âèñ‰∫ã‰ª∂
  getEventsForDate(day.date);
};

// Ëé∑ÂèñÈÄâ‰∏≠Êó•ÊúüÁöÑ‰∫ã‰ª∂
const currentEvents = ref([]);

const getEventsForDate = (date) => {
  if (!date) return;
  
  // ËøôÈáåÂèØ‰ª•Ë∞ÉÁî®APIËé∑ÂèñËØ•Êó•ÊúüÁöÑ‰∫ã‰ª∂Êï∞ÊçÆ
  // Ê®°ÊãüÊï∞ÊçÆ
  setTimeout(() => {
    const events = eventList.value.filter(event => {
      const eventDate = new Date(event.date);
      return eventDate.getDate() === date.getDate() && 
             eventDate.getMonth() === date.getMonth() && 
             eventDate.getFullYear() === date.getFullYear();
    });
    
    currentEvents.value = events;
  }, 100);
};

// Ê∑ªÂä†Êñ∞‰∫ã‰ª∂
const newEventTitle = ref('');
const newEventTime = ref('');
const showAddEventForm = ref(false);

const toggleAddEventForm = () => {
  showAddEventForm.value = !showAddEventForm.value;
};

const addNewEvent = () => {
  if (!selectedDate.value || !newEventTitle.value) return;
  
  const newEvent = {
    id: Date.now(),
    title: newEventTitle.value,
    date: selectedDate.value,
    time: newEventTime.value,
    completed: false
  };
  
  eventList.value.push(newEvent);
  currentEvents.value.push(newEvent);
  
  // ÈáçÁΩÆË°®Âçï
  newEventTitle.value = '';
  newEventTime.value = '';
  showAddEventForm.value = false;
  
  // Êõ¥Êñ∞Êó•ÂéÜËßÜÂõæ
  calendarDays.value.forEach(day => {
    if (day.date && 
        day.date.getDate() === selectedDate.value.getDate() && 
        day.date.getMonth() === selectedDate.value.getMonth() && 
        day.date.getFullYear() === selectedDate.value.getFullYear()) {
      day.hasEvent = true;
    }
  });
};

// Ê®°Êãü‰∏Ä‰∫õ‰∫ã‰ª∂Êï∞ÊçÆ
const initEventData = () => {
  const today = new Date();
  const tomorrow = new Date();
  tomorrow.setDate(today.getDate() + 1);
  
  eventList.value = [
    {
      id: 1,
      title: '‰∫ßÂìÅËØÑÂÆ°‰ºöËÆÆ',
      date: today,
      time: '10:00',
      completed: false
    },
    {
      id: 2,
      title: 'ÂÆ¢Êà∑ÊãúËÆø',
      date: tomorrow,
      time: '14:30',
      completed: false
    },
    {
      id: 3,
      title: 'Âõ¢ÈòüÂë®‰ºö',
      date: today,
      time: '16:00',
      completed: true
    }
  ];
  
  // ÈªòËÆ§ÈÄâ‰∏≠‰ªäÂ§©
  selectedDate.value = today;
  getEventsForDate(today);
};

// ÂÆåÊàê/ÂèñÊ∂àÂÆåÊàê‰∫ã‰ª∂
const toggleEventCompleted = (event) => {
  event.completed = !event.completed;
};

// Âà†Èô§‰∫ã‰ª∂
const deleteEvent = (eventId) => {
  eventList.value = eventList.value.filter(event => event.id !== eventId);
  currentEvents.value = currentEvents.value.filter(event => event.id !== eventId);
  
  // Êõ¥Êñ∞Êó•ÂéÜËßÜÂõæ‰∏≠ÁöÑ‰∫ã‰ª∂Ê†áËÆ∞
  updateCalendarEvents();
};

// Êõ¥Êñ∞Êó•ÂéÜ‰∏≠ÁöÑ‰∫ã‰ª∂Ê†áËÆ∞
const updateCalendarEvents = () => {
  calendarDays.value.forEach(day => {
    if (!day.date) return;
    
    day.hasEvent = eventList.value.some(event => {
      const eventDate = new Date(event.date);
      return eventDate.getDate() === day.date.getDate() && 
             eventDate.getMonth() === day.date.getMonth() && 
             eventDate.getFullYear() === day.date.getFullYear();
    });
  });
};

// ‰ªé‰∫ëÂáΩÊï∞Ëé∑ÂèñÈ¶ñÈ°µËÆæÁΩÆ
const getIndexSettings = async () => {
  try {
    isLoadingSettings.value = true;
    
    const indexYunApi = uniCloud.importObject('indexYun', { customUI: true });
    const result = await indexYunApi.getIndexSettings();
    
    if (result.code === 0) {
      indexSettings.value = {
        showHomeButton: result.showHomeButton,
        homeButtonText: result.homeButtonText || 'ËøîÂõûÈ¶ñÈ°µ',
        homeButtonIcon: result.homeButtonIcon || 'üè†'
      };
    }
  } catch (error) {
    console.error('Ëé∑ÂèñÈ¶ñÈ°µËÆæÁΩÆÂ§±Ë¥•:', error);
  } finally {
    isLoadingSettings.value = false;
  }
};

// Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
const goToIndexPage = () => {
  uni.switchTab({
    url: '/pages/index/index'
  });
};

// ÁõëÂê¨È¶ñÈ°µÊåâÈíÆÁä∂ÊÄÅÂèòÂåñ
onMounted(() => {
  getIndexSettings();
  
  // ÁõëÂê¨Êù•Ëá™ÁÆ°ÁêÜÈ°µÈù¢ÁöÑÁä∂ÊÄÅÂèòÂåñ
  uni.$on('homeButtonChanged', (newState) => {
    indexSettings.value.showHomeButton = newState;
  });
});

// Âú®ÁªÑ‰ª∂ÈîÄÊØÅÊó∂ÁßªÈô§‰∫ã‰ª∂ÁõëÂê¨
onUnmounted(() => {
  uni.$off('homeButtonChanged');
});

// ÂàùÂßãÂåñ
onMounted(() => {
  initEventData();
});
</script>

<template>
  <view class="page-container">
    <!-- Êó•ÂéÜÈÉ®ÂàÜ -->
    <view class="calendar-container">
      <!-- Êó•ÂéÜÂ§¥ÈÉ® -->
      <view class="calendar-header">
        <view class="header-title">
          <text class="year-month">{{ currentYear }}Âπ¥{{ currentMonth }}Êúà</text>
        </view>
        <view class="header-actions">
          <text class="action-btn" @click="goToPreviousMonth">‰∏ä‰∏™Êúà</text>
          <text class="action-btn today-btn" @click="goToToday">‰ªäÂ§©</text>
          <text class="action-btn" @click="goToNextMonth">‰∏ã‰∏™Êúà</text>
        </view>
      </view>
      
      <!-- ÊòüÊúüÊ†áÈ¢ò -->
      <view class="weekdays">
        <text class="weekday">Êó•</text>
        <text class="weekday">‰∏Ä</text>
        <text class="weekday">‰∫å</text>
        <text class="weekday">‰∏â</text>
        <text class="weekday">Âõõ</text>
        <text class="weekday">‰∫î</text>
        <text class="weekday">ÂÖ≠</text>
      </view>
      
      <!-- Êó•ÂéÜÁΩëÊ†º -->
      <view class="calendar-grid">
        <view 
          v-for="(day, index) in calendarDays" 
          :key="index" 
          :class="[
            'calendar-day', 
            !day.isCurrentMonth ? 'inactive-day' : '',
            day.isToday ? 'today' : '',
            day.isSelected ? 'selected-day' : '',
            day.hasEvent ? 'has-event' : ''
          ]"
          @click="selectDate(day)"
        >
          <text class="day-number">{{ day.day }}</text>
          <view v-if="day.hasEvent" class="event-dot"></view>
        </view>
      </view>
      
      <!-- ÈÄâ‰∏≠Êó•ÊúüÁöÑ‰∫ã‰ª∂ÂàóË°® -->
      <view class="events-container">
        <view class="events-header">
          <text class="events-title">
            {{ selectedDate ? selectedDate.getFullYear() + 'Âπ¥' + (selectedDate.getMonth() + 1) + 'Êúà' + selectedDate.getDate() + 'Êó•' : 'Êú™ÈÄâÊã©Êó•Êúü' }}
          </text>
          <view class="add-event-btn" @click="toggleAddEventForm">
            <text class="add-icon">+</text>
            <text>Ê∑ªÂä†Ë°åÁ®ã</text>
          </view>
        </view>
        
        <!-- Ê∑ªÂä†‰∫ã‰ª∂Ë°®Âçï -->
        <view v-if="showAddEventForm" class="add-event-form">
          <input 
            type="text" 
            class="event-input" 
            placeholder="Ë°åÁ®ãÂÜÖÂÆπ" 
            v-model="newEventTitle" 
          />
          <input 
            type="time" 
            class="event-input" 
            placeholder="Êó∂Èó¥" 
            v-model="newEventTime" 
          />
          <view class="form-actions">
            <button class="cancel-btn" @click="toggleAddEventForm">ÂèñÊ∂à</button>
            <button class="confirm-btn" @click="addNewEvent">Á°ÆÂÆö</button>
          </view>
        </view>
        
        <!-- ‰∫ã‰ª∂ÂàóË°® -->
        <view v-if="currentEvents.length > 0" class="event-list">
          <view 
            v-for="event in currentEvents" 
            :key="event.id" 
            :class="['event-item', event.completed ? 'completed-event' : '']"
          >
            <view class="event-time">{{ event.time }}</view>
            <view class="event-content">
              <text class="event-title">{{ event.title }}</text>
              <view class="event-actions">
                <text class="complete-btn" @click="toggleEventCompleted(event)">
                  {{ event.completed ? 'Â∑≤ÂÆåÊàê' : 'ÂÆåÊàê' }}
                </text>
                <text class="delete-btn" @click="deleteEvent(event.id)">Âà†Èô§</text>
              </view>
            </view>
          </view>
        </view>
        
        <!-- Ê≤°Êúâ‰∫ã‰ª∂Êó∂ÊòæÁ§∫ -->
        <view v-else class="no-events">
          <text class="no-events-text">‰ªäÂ§©Ê≤°ÊúâË°åÁ®ãÂÆâÊéí</text>
        </view>
      </view>
      
      <!-- Â∫ïÈÉ®ËøîÂõûÈ¶ñÈ°µÊåâÈíÆ -->
      <view v-if="indexSettings.showHomeButton" class="bottom-button-container">
        <button class="home-button" @click="goToIndexPage">
          <text class="home-icon">{{ indexSettings.homeButtonIcon }}</text>
          <text class="home-text">{{ indexSettings.homeButtonText }}</text>
        </button>
      </view>
    </view>
  </view>
</template>

